import React from 'react';
import './style.scss'

import {Link} from 'react-router-dom'


import { YMaps, Map, Placemark, Panorama, FullscreenControl } from 'react-yandex-maps';



function Vt(props) {

    const FirstMap = () => ( 
        <YMaps>
            <Map defaultState = {{ center: [53.516168, 49.303711], zoom: 16, height: 50, width: 50 } }>
                <Panorama defaultPoint = {[53.516168, 49.303711] }/> 
                <Placemark geometry = {[53.516168, 49.303711] }/>
                <FullscreenControl fullscreen = {true}/>
            </Map> 
        </YMaps>
    );

    return (
        <div className="map_container">
            <div className="map"><FirstMap></FirstMap></div>
            <p className="white ex">ООО «Виктори Групп»</p>
            <p className="white small">Самарская обл. г.Тольятти</p>
            <div className="phone_max">
                <p className="white small"><strong>почта:</strong> office@gruzoviktory.ru</p>
                <p className="white small"><strong>сайт:</strong> gruzoviktory.ru</p>
            </div>
            <div className="phone">
                <p className="white small"><strong>тел:</strong> 8 (8482) 65-04-01 (доб. 204)</p>
                <p className="white small"><strong>тел:</strong> 8 (800) 707-66-18</p>
            </div>
            <Link to="/victori_group/group">
                  <p>назад</p>
            </Link>
        </div>
        
    )
}

export default Vt()